/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.main {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
}
.main.float .main__body {
  padding: 0;
}
.main__header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 52px;
  z-index: 999;
}
.main__body {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 52px 0 0 76px;
}
.main__body.full-width {
  padding: 52px 0 0 0;
}
.main__body-sider {
  width: 76px;
  height: calc(100% - 52px);
  position: absolute;
  top: 52px;
  left: 0;
  z-index: 998;
}
.main__body-content {
  width: 100%;
  height: 100%;
}
.main__backend {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 52px;
}
