/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.header {
  width: 100%;
  height: 100%;
  padding: 0 24px;
  background-color: #f5f5f5;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  transition: background-color 0.2s ease;
}
.header.login {
  background-color: rgba(72, 133, 255, 0.2);
}
.header__left,
.header__right {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.header__left {
  float: left;
}
.header__left-logo {
  width: auto;
  height: 38px;
  margin-right: 16px;
}
.header__left-slogan {
  font-size: 14px;
  font-weight: bolder;
  color: #333333;
}
.header__left-menus.ant-menu {
  background-color: transparent;
  border-bottom-color: transparent;
}
.header__right {
  float: right;
}
.header__right-icon {
  font-size: 22px !important;
  color: #333333 !important;
  cursor: pointer;
}
.header__right-datav {
  font-size: 0;
  margin-right: 36px;
}
.header__right-user {
  font-size: 0;
  margin-left: 36px;
  margin-right: 26;
}
